.box {
  width: 140px;
  height: 30px;
  float: left;
  text-align: center;
  text-transform: uppercase;
  background: none;
  justify-content: center;
  align-items: center;
  margin: 0 5px;
  padding: 15px;
  font-size: 15px;
  font-weight: 900;
  transition: all .5s linear;
  display: flex;
  position: relative;
  overflow: hidden;
}

.box:before {
  content: "";
  height: 3px;
  width: 100%;
  box-sizing: border-box;
  border-bottom: 3px solid rgba(0, 0, 0, 0);
  border-left: 3px solid rgba(0, 0, 0, 0);
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translateX(100%);
}

.box:after {
  content: "";
  width: 100%;
  height: 3px;
  box-sizing: border-box;
  border-top: 3px solid rgba(0, 0, 0, 0);
  border-right: 3px solid rgba(0, 0, 0, 0);
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-100%);
}

.box:hover {
  box-shadow: 0 5px 15px rgba(0, 0, 0, .5);
}

.box:hover:before {
  height: 100%;
  border-color: #262626;
  transition: transform .3s linear, height .3s linear .3s;
  transform: translateX(0);
}

.box:hover:after {
  height: 100%;
  border-color: #262626;
  transition: transform .3s linear, height .3s linear .5s;
  transform: translateX(0);
}

